import { Injectable, Type } from "@angular/core";
import { Router } from "@angular/router";
import { Subject, BehaviorSubject, AsyncSubject } from "rxjs";
import { UserDto } from "../model";
import { FriendsRequestDto, FriendsDto } from "../model/friends";

@Injectable({
  providedIn: "root"
})
export class DataSharingService {
  private _stack: SharingStackItem[] = [];
  //todo refactor
  isClosed = new Subject<boolean>();
  count = new Subject<number>();
  private getUser = new BehaviorSubject("default");
  private loggedUser = new BehaviorSubject(null);
  private friendRequestsSubject = new BehaviorSubject([]);
  private matchRequestsSubject = new BehaviorSubject([]);
  private incomingMatchesSubject = new BehaviorSubject([]);
  private friendsSubject = new BehaviorSubject([]);

  currentUser = this.getUser.asObservable();
  currentLoggedUser = this.loggedUser.asObservable();
  friendRequests = this.friendRequestsSubject.asObservable();
  matchRequests = this.matchRequestsSubject.asObservable();
  incomingMatches = this.incomingMatchesSubject.asObservable();
  friends = this.friendsSubject.asObservable();
  name = new BehaviorSubject<string>("");
  constructor(private router: Router) {}

  changeFriendRequests(friendRequests: FriendsRequestDto[]) {
    this.friendRequestsSubject.next(friendRequests);
  }

  changeMatchRequests(matchRequests) {
    this.matchRequestsSubject.next(matchRequests);
  }

  changeIncomingMatches(incomingMatches) {
    this.incomingMatchesSubject.next(incomingMatches);
  }
  changeFriends(friends: FriendsDto[]) {
    this.friendsSubject.next(friends);
  }

  changeUser(message: string) {
    this.getUser.next(message);
  }
  setLoggedUser(userDto: UserDto) {
    this.loggedUser.next(userDto);
  }

  push(item: SharingStackItem) {
    this._stack.push(item);
  }

  pop() {
    if (this._stack.length) {
      return this._stack.pop();
    }
    return undefined;
  }

  getStack() {
    return this._stack;
  }

  clearStack() {
    this._stack = [];
  }

  isStackEmpty() {
    return this._stack.length === 0;
  }

  shouldStackBeCleared(destinationUrl: string) {
    return (
      !this.isStackEmpty() &&
      !(
        this.shouldStateBeRestored(destinationUrl) ||
        this.areUrlsEqual(this.getLast().destinationUrl, destinationUrl)
      )
    );
  }

  shouldStateBeRestored(currentUrl: string) {
    return (
      !this.isStackEmpty() &&
      this.areUrlsEqual(this.getLast().returnUrl, currentUrl)
    );
  }

  updateStackAndProceed(outValue: any, outType?: Type<any>) {
    if (!this.isStackEmpty()) {
      this.getLast().out = {
        value: outValue,
        type: outType
      };
      this.proceed();
    }
  }

  proceed() {
    if (!this.isStackEmpty()) {
      this.router.navigate([this.getLast().returnUrl]);
    }
  }

  getLast() {
    return this._stack[this._stack.length - 1];
  }

  private areUrlsEqual(url1: string, url2: string) {
    return this.removeQueryParams(url1) === this.removeQueryParams(url2);
  }

  private removeQueryParams(url: string): string {
    if (url && url.indexOf("?") >= 0) {
      return url.substring(0, url.indexOf("?"));
    } else {
      return url;
    }
  }
}

export interface SharingStackItem {
  out?: {
    value: any;
    type?: Type<any>;
  };
  currentState?: any;
  destinationUrl: string;
  returnUrl: string;
}
