import { Component, OnInit, Input } from "@angular/core";
import { NotificationDto, UserDto } from "@app/shared/model";
import { Router, ActivatedRoute } from "@angular/router";
import { DataSharingService } from "@app/shared/service/data-sharing.service";
import { AuthService } from "@app/core/service";
import { UserService } from "@app/shared/service/user.service";
import { NotificationService } from "@app/shared/service/notification.service";
import { MessageService } from "@app/shared/service/message.service";
import { MatDialog } from "@angular/material";
import { MessageDialogComponent } from "../message-dialog/message-dialog.component";
import { FriendsService } from "@app/shared/service/friends.service";
import { InviteService } from "@app/shared/service/invite.service";
import { MatchService } from "@app/shared/service/match.service";
import { ToastrService } from "ngx-toastr";
import { TeamsDialogComponent } from "@app/functionalities/playing-field/components/teams-dialog/teams-dialog.component";
import { TeamService } from "@app/shared/service/team.service";
import { IfStmt } from "@angular/compiler";
import { DomSanitizer } from "@angular/platform-browser";

@Component({
  selector: "app-notifications",
  templateUrl: "./notifications.component.html",
  styleUrls: ["./notifications.component.scss"]
})
export class NotificationsComponent implements OnInit {
  activeNotifications = false;
  notifications: NotificationDto[] = [];
  @Input() loggedUser: UserDto;
  count: number = 0;
  userId;

  constructor(
    private router: Router,
    private dialog: MatDialog,
    private dataSharingService: DataSharingService,
    private authService: AuthService,
    private userService: UserService,
    private messageService: MessageService,
    private notificationService: NotificationService,
    private friendsService: FriendsService,
    private inviteService: InviteService,
    private matchService: MatchService,
    private toastrService: ToastrService,
    private teamService: TeamService,
    private activatedRoute: ActivatedRoute,
    private domSanitizer: DomSanitizer
  ) {}
  ngOnInit() {
    this.getNotifications();
    this.activatedRoute.params.subscribe(param => {
      this.userId = param["id"];
    });
  }

  getNotifications() {
    this.activeNotifications = !this.activeNotifications;
    this.notificationService.getNotifications().subscribe(notifications => {
      this.notifications = notifications;
      this.notifications.forEach(val => {
        if (val.displayed) {
          let word;
          if (val.accepted) {
            word = "Zaakceptowae";
          } else {
            word = "Odrzucie";
          }
          val.title = val.title.replace("Otrzymae", word);
        } else {
          this.count += 1;
        }
      });
      this.dataSharingService.count.next(this.count);
    });
  }

  setAsDisplayed(notification, accepted) {
    if (!notification.displayed) {
      this.notificationService
        .setAsDisplayed(notification.id, accepted)
        .subscribe(() => {
          notification.displayed = true;
          this.dataSharingService.count.next(this.count - 1);
        });
    }
  }
  setAllAsDisplayed(event) {
    event.stopPropagation();
    event.preventDefault();
    this.notificationService.setAllAsDisplayed().subscribe(() => {
      this.notifications.forEach(val => {
        val.displayed = true;
      });
    });
  }
  openMessage(notification) {
    this.setAsDisplayed(notification, false);
    this.messageService.getMessages(notification.senderId).subscribe(val => {
      this.dialog.open(MessageDialogComponent, {
        width: "620px",
        data: {
          messages: val,
          id: notification.senderId,
          loggedUser: this.loggedUser,
          username: notification.senderUsername
        },
        panelClass: "custom-dialog-container"
      });
    });
  }

  goToUserProfile(user) {
    this.dataSharingService.changeUser(user.id);
    this.router.navigate(["user/" + user.id]);
  }

  acceptFriendRequest(event, notification) {
    event.stopPropagation();
    event.preventDefault();

    this.friendsService
      .acceptRequest(notification.entityId)
      .subscribe(friend => {
        friend.avatar = this.domSanitizer.bypassSecurityTrustResourceUrl(
          "data:image/png;base64," + friend.avatar
        );

        notification.accepted = true;
        notification.title = notification.title.replace(
          "Otrzymae",
          "Zaakceptowae"
        );
        this.setAsDisplayed(notification, true);
        this.dataSharingService.friends.subscribe(friends => {
          if (friends.length) {
            if (friends.find(val => val !== friend)) {
              this.dataSharingService.changeFriends(friends);
            }
          } else {
            friends.push(friend);
            this.dataSharingService.changeFriends(friends);
          }
        });

        this.dataSharingService.changeFriendRequests([]);
        this.dataSharingService.friendRequests.subscribe(val => {
          if (val.length) {
            if (
              val.find(
                request => request.senderUsename === notification.senderUsername
              )
            ) {
              val.splice(
                val.indexOf(
                  val.find(
                    request =>
                      request.senderUsername === notification.senderUsername
                  ),
                  1
                )
              );
            }
            this.dataSharingService.changeFriendRequests(val);
          }
        });
      });
  }
  declineFriendRequest(event, notification) {
    event.stopPropagation();
    event.preventDefault();

    this.friendsService.removeRequest(notification.entityId).subscribe(val => {
      this.setAsDisplayed(notification, false);
      notification.title = notification.title.replace(
        "Otrzymae",
        "Odrzucie"
      );
    });
  }
  acceptMatchRequest(event, notification) {
    event.stopPropagation();
    event.preventDefault();

    this.matchService.getMatchById(notification.externalId).subscribe(match => {
      if (match.ownerId != null) {
        if (match.size < match.maxSize) {
          this.inviteService
            .acceptInvite(notification.entityId)
            .subscribe(val => {
              this.toastrService.success("Zaakceptowae zaproszenie do meczu");
              this.setAsDisplayed(notification, true);
              notification.title = notification.title.replace(
                "Otrzymae",
                "Zaakceptowae"
              );

              // this.dataSharingService.incomingMatches.subscribe(
              //   incomingMatches => {
              //     if (incomingMatches.length) {
              //       if (incomingMatches.find(inc => inc === match)) {
              //         this.dataSharingService.changeIncomingMatches(
              //           incomingMatches
              //         );
              //       }else {
              //         incomingMatches.push(match);
              //         this.dataSharingService.changeIncomingMatches(
              //           incomingMatches
              //         );
              //       }
              //     }
              //   }
              // );

              // this.dataSharingService.changeMatchRequests([]);
              // this.dataSharingService.matchRequests.subscribe(val => {
              //   if (val.length) {
              //     if (
              //       val.find(
              //         request =>
              //           request.senderUsename === notification.senderUsername
              //       )
              //     ) {
              //       val.splice(
              //         val.indexOf(
              //           val.find(
              //             request =>
              //               request.senderUsername ===
              //               notification.senderUsername
              //           ),
              //           1
              //         )
              //       );
              //     }
              //     this.dataSharingService.changeMatchRequests(val);
              //   }
              // });
            });
        } else {
          this.toastrService.error("Druyna jest pena!");
        }
      } else {
        this.toastrService.error("Mecz nie istnieje");
      }
    });
  }

  declineMatchRequest(event, notification) {
    event.stopPropagation();
    event.preventDefault();

    this.inviteService.declineInvite(notification.entityId).subscribe(() => {
      this.setAsDisplayed(notification, false);
      notification.title = notification.title.replace(
        "Otrzymae",
        "Odrzucie"
      );
    });
  }

  openTeamDialog(notification, join: boolean) {
    this.matchService.getMatchById(notification.entityId).subscribe(match => {
      let teams;
      this.teamService.getTeamsByMatch(match.id).subscribe(val => {
        teams = val;

        if (val.length) {
          if (
            join &&
            teams &&
            !teams[0].players.find(val => val.id === this.loggedUser.id) &&
            !teams[1].players.find(val => val.id === this.loggedUser.id)
          ) {
            if (teams[0].size < teams[0].maxSize) {
              teams[0].players.push({
                id: this.loggedUser.id,
                username: this.loggedUser.username,
                position: this.loggedUser.position
              });
            } else if (teams[1].size < teams[1].maxSize) {
              teams[1].players.push({
                id: this.loggedUser.id,
                username: this.loggedUser.username,
                position: this.loggedUser.position
              });
            } else {
              this.toastrService.success(
                "Nie moesz doczy do meczu, jest peen"
              );
            }
          }

          this.dialog.open(TeamsDialogComponent, {
            width: "800px",
            data: {
              teamsDto: teams,
              isPrivate: match.isPrivate,
              matchId: match.id,
              editable: match.editable
            }
          });
        } else {
          this.toastrService.error("Mecz nie istnieje");
        }
      });
      this.setAsDisplayed(notification, false);
    });
  }
}
