import {
  Component,
  OnInit,
  Inject,
  ElementRef,
  ViewChild,
  HostListener
} from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MessageDto } from "@app/shared/model/message";
import { UserDto } from "@app/shared/model";
import { AngularEditorConfig } from "@kolkov/angular-editor";
import { FormControl } from "@angular/forms";
import { MessageService } from "@app/shared/service/message.service";
import { AuthService } from "@app/core/service";
import { UserService } from "@app/shared/service/user.service";
import * as moment from "moment";
import { FriendsService } from "@app/shared/service/friends.service";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-message-dialog",
  templateUrl: "./message-dialog.component.html",
  styleUrls: ["./message-dialog.component.scss"]
})
export class MessageDialogComponent implements OnInit {
  content = new FormControl("");
  @ViewChild("scrollMe", { static: true })
  private myScrollContainer: ElementRef;
  sended: boolean;
  isActualFriend = false;
  constructor(
    @Inject(MAT_DIALOG_DATA)
    public data: {
      messages: MessageDto[];
      id: number;
      loggedUser: UserDto;
      username: string;
    },
    private dialogRef: MatDialogRef<MessageDialogComponent>,
    private messageService: MessageService,
    private authService: AuthService,
    private userService: UserService,
    private friendsService: FriendsService,
    private toastrService: ToastrService
  ) {}

  ngOnInit() {
    this.friendsService.getSendedRequests().subscribe(friendsRequests => {
      const sendedFriendRequests = friendsRequests;
      if (sendedFriendRequests.indexOf(this.data.username) !== -1) {
        this.sended = true;
      }
    });

    this.friendsService.isFriend(this.data.id).subscribe(val => {
      this.isActualFriend = val;
    });
  }

  sendMessage() {
    if (this.content.value !== "") {
      this.messageService
        .sendMessage(this.data.id, this.content.value)
        .subscribe(val => {
          this.data.messages.push(val);
          this.content.setValue("");
        });
    }
  }
  @HostListener("document:keydown.enter", ["$event"]) onKeydownHandler(
    event: KeyboardEvent
  ) {
    if (this.content.value !== "") {
      this.messageService
        .sendMessage(this.data.id, this.content.value)
        .subscribe(val => {
          this.data.messages.push(val);
          this.content.setValue("");
        });
    }
  }
  addToFriends() {
    if (this.sended) {
      this.toastrService.error(
        "Wysae ju zaproszenie do znajomych do tego uytkownika"
      );
    } else if (this.isActualFriend) {
      this.toastrService.error("Uytkownik jest ju twoim znajomym");
    } else {
      this.friendsService.sendRequest(this.data.id).subscribe(() => {
        this.sended = true;
        this.toastrService.success("Wysano zaproszenie do znajomych");
      });
    }
  }
  editorConfig: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: "auto",
    minHeight: "90px",
    maxHeight: "150px",
    width: "auto",
    minWidth: "0",
    translate: "yes",
    enableToolbar: true,
    showToolbar: true,
    placeholder: "Enter text here...",
    defaultParagraphSeparator: "",
    defaultFontName: "",
    defaultFontSize: "",
    fonts: [
      { class: "arial", name: "Arial" },
      { class: "times-new-roman", name: "Times New Roman" },
      { class: "calibri", name: "Calibri" },
      { class: "comic-sans-ms", name: "Comic Sans MS" }
    ],
    customClasses: [
      {
        name: "quote",
        class: "quote"
      },
      {
        name: "redText",
        class: "redText"
      },
      {
        name: "titleText",
        class: "titleText",
        tag: "h1"
      }
    ],
    sanitize: false,
    toolbarPosition: "top",
    toolbarHiddenButtons: [["bold", "italic"], ["fontSize"]]
  };
}
