import { Injectable, OnDestroy } from "@angular/core";
import {
  Router,
  CanActivate,
  CanLoad,
  NavigationEnd,
  UrlSegment
} from "@angular/router";
import { AuthService } from "./auth.service";
import { Subscription } from "rxjs";
import { Route } from "@angular/compiler/src/core";
import { DataSharingService } from "@app/shared/service/data-sharing.service";
@Injectable()
export class AuthGuardWorkerService implements CanActivate, CanLoad, OnDestroy {
  constructor(
    public auth: AuthService,
    public router: Router,
    private dataSharingService: DataSharingService
  ) {}
  subscription = new Subscription();

  canActivate(): boolean {
    this.dataSharingService.currentLoggedUser.subscribe(val => {
      if (!val) {
        this.router.navigate(["/search"]);
      }
    });
    if (!this.auth.isLogged()) {
      this.router.navigate(["/search"]);
      return false;
    }
    return true;
  }
  canLoad(router: Route, segments: UrlSegment[]) {
    if (!this.auth.isLogged()) {
      this.router.navigate(["/search"]);
      return false;
    } else {
      if (segments[2].path === localStorage.getItem("playingFieldId")) {
        return true;
      } else {
        this.router.navigate(["/search"]);
        return false;
      }
    }
  }
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
